.page-eec-test {
    padding-top: 60px;
    iframe {
        border: none;
        width: 100%;
        height: 100vh;
    }

    .not-found {
        width: 100%;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #e74c3c;
    }

    .test-content {
        border-left: solid 1px rgba(#000, 0.2);
        width: 100%;
        height: 100vh;
        overflow-y: scroll;
        padding: 40px;
        font-size: 18px;

        .test-section {
            .section-q {
                display: flex;
                margin-bottom: 20px;
                b {
                    margin-right: 20px;
                }
            }
        }
    }

    .test-header {
        position: fixed;
        width: 100%;
        height: 60px;
        top: 0;
        left: 0;
        right: 0;
        background: #34495e;
        color: #fff;
        padding: 0 20px;
        display: flex;
        align-items: center;
        font-size: 18px;
        box-shadow: 0 10px 30px rgba(#000, 0.5);
        z-index: 1;

        a {
            color: #fff;
            margin-right: 20px;
            &:hover {
                opacity: 0.8;
            }
        }

        .v {
            position: absolute;
            top: -5px;
            right: -7px;
            text-transform: uppercase;
            font-size: 36px;
            line-height: 1;
        }
    }

    .s2 {
        display: flex;
        margin-bottom: 20px;

        section {
            flex: 1;
        }
    }
}
