.comp-header {
    margin: 0;
    padding: 40px 0 0 0;
    .header-menu {
        // margin-bottom: 20px;
        a {
            margin-left: 20px;
        }
    }

    .logo {
        display: flex;
        align-items: center;
        img {
            height: 80px;
            margin-right: 20px;
        }
        span {
            text-transform: uppercase;
            font-weight: bold;
            font-size: 21px;
            color: #000;
        }
    }
}
