.page-subject {
    padding: 0;

    .btn-class {
        margin-bottom: 20px;
    }

    .subject-detail {
        position: relative;
        cursor: pointer;
        &:hover {
            background: rgba(#000, 0.05);
        }

        &:active {
            background: rgba(#000, 0.1);
        }

        img {
            margin: 10px;
        }
    }

    .custom-card {
        // margin-bottom: 20px;

        .ant-card-body {
            padding: 0;
        }

        .ant-card-cover {
            height: 150px;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            border: solid 1px rgba(#000, 0.05);
        }
    }
}

.modal-player {
    position: relative;
    top: -24px;
    left: -24px;
    right: -24px;
    width: calc(100% + 48px);
}

.modal-files {
    position: relative;
    left: -24px;
    right: -24px;
    width: calc(100% + 48px);
}
